<template>
  <div id="app">
    <div
      class="outter"
      :class="{ hideLeft: $route.path.split('/').length > 2 }"
    >
      <!-- 四个门面页 -->
      <section class="app-content">
        <keep-alive>
          <router-view name="default"></router-view>
        </keep-alive>
      </section>
      <!--底部导航 路由 -->
    </div>

    <!--其他店内页集合 有过渡效果-->
    <transition
      name="custom-classes-transition"
      :enter-active-class="enterAnimate"
      :leave-active-class="leaveAnimate"
    >
      <router-view name="subPage" class="sub-page"></router-view>
    </transition>
    <van-overlay class-name="customer-overlay" :show="showLoading">
      <!-- <van-overlay class-name="customer-overlay" :show="true"> -->
      <van-loading size="24px" color="#fff" vertical>加载中...</van-loading>
    </van-overlay>
  </div>
</template>

<script>
import mixin from "./utils/mixin.js" // 混合被单独放在 mixin.js 中管理
window.mixin = mixin // 将 混合/mixin 暴露在窗口对象中，某些组件需要时，直接提取 window.mixin
export default {
  name: 'app',
  components: {
  },
  computed: {
    showLoading() {
      return this.$store.state.axiosLoading
    }
  },
  data() {
    return {
      "pageName": "",
      "routerAnimate": false,
      "enterAnimate": "", //页面进入动效
      "leaveAnimate": "" //页面离开动效
    }
  },
  watch: {
    // 监听 $route 为店内页设置不同的过渡效果
    "$route"(to, from) {
      const toDepth = to.path.split('/').length
      const fromDepth = from.path.split('/').length
      if (toDepth === 2) {
        // this.$store.commit("setPageName", to.name)
      }
      //同一级页面无需设置过渡效果
      if (toDepth === fromDepth) {
        return;
      }
      this.enterAnimate = toDepth > fromDepth ? "animated fadeInRight" : "animated fadeInLeft"
      this.leaveAnimate = toDepth > fromDepth ? "animated fadeOutLeft" : "animated fadeOutRight"
      // 从店面页进入店内页 需要对店内页重新设置离开动效 因为他们处于不同 name 的 router-view
      if (toDepth === 3) {
        this.leaveAnimate = "animated fadeOutRight"
      }
    }
  },
  created() {
    console.log(this.showLoading)
  },
  methods: {
  }
}
</script>
